/* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */
@font-face {
  src: url("../fonts/Jost-Regular.woff2") format("woff2");
  font-family: "Jost";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  src: url("../fonts/Jost-Medium.woff2") format("woff2");
  font-family: "Jost";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  src: url("../fonts/Jost-Bold.woff2") format("woff2");
  font-family: "Jost";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  src: url("../fonts/Jost-ExtraBold.woff2") format("woff2");
  font-family: "Jost";
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
.page {
  height: 100%;
  font-family: var(--font-family);
  font-weight: 400;
  font-style: normal;
  font-feature-settings: "pnum" on, "lnum" on;
  font-size: 1.3vw;
  line-height: 1.6;
  letter-spacing: 0.04em;
  -webkit-text-size-adjust: 100%;
  color: var(--color-white);
  background-color: var(--color-bg);
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
}
.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}
@media (min-width: 120em) {
  .page {
    font-size: 22px;
  }
}
@media (max-width: calc(80.0625em - 0.0625em)) {
  .page {
    font-size: 16px;
  }
}

.site-container {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: auto 1fr auto;
  min-height: calc(100vh - var(--header-height) * 2 + 47px);
}

.hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.sr-only {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.section {
  margin-bottom: clamp(100px, 11.875rem, 190px);
}

.input-reset {
  border-color: transparent;
  background-color: var(--color-noactive);
}

.logo {
  transform: translateX(-62%);
}

.header {
  position: fixed;
  top: 0;
  z-index: 1;
  border-bottom: 0.5px solid var(--color-black);
  width: 100%;
  background-color: rgba(27, 27, 27, 0.8);
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 37px 0 30px;
}

.hero {
  background-image: url("../img/hero.jpg.jpg");
  /* stylelint-disable-next-line declaration-block-no-duplicate-properties */
  background-image: image-set("../img/hero.jpg.avif" type("image/avif"), "../img/hero.jpg.webp" type("image/webp"), "../img/hero.jpg.jpg" type("image/jpg"));
  display: flex;
  align-items: center;
  position: relative;
  min-height: 900px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero::before {
  content: "";
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-black);
  opacity: 0.36;
  filter: blur(5px);
}
.hero__content {
  position: relative;
  padding-top: 4.0625rem;
}
.hero__title {
  margin-bottom: 10px;
}
.hero__text {
  margin-bottom: 2.625rem;
  letter-spacing: 0.04em;
}
.hero__mouse {
  position: relative;
  display: block;
  margin: 0 auto;
  border: 2px solid var(--color-white);
  border-radius: 100px;
  width: 40px;
  height: 60px;
}
.hero__mouse::before {
  content: "";
  position: absolute;
  left: 1px;
  right: 0;
  margin: 0 auto;
  width: 2px;
  height: 19px;
  background-color: var(--color-white);
  animation: mouse 2s linear 0.1s infinite;
}

@keyframes mouse {
  0% {
    top: 5px;
    opacity: 0;
  }
  100% {
    top: 30px;
    opacity: 1;
  }
}
.tabs__content {
  margin-bottom: clamp(20px, 2.1875rem, 50px);
}
.tabs__cards {
  display: grid;
  column-gap: 30px;
  grid-template-columns: repeat(4, 1fr);
}
.tabs__btns {
  display: flex;
  margin-bottom: clamp(20px, 3.125rem, 50px);
}
.tabs__btns li:not(:last-child) {
  margin-right: clamp(15px, 1.875rem, 30px);
}
.tabs__btn {
  border: 1px solid transparent;
  padding: 9px clamp(15px, 1.75rem, 28px);
  font-weight: 400;
  font-size: 18px;
  letter-spacing: 0.06em;
  color: var(--color-gray);
  background-color: var(--color-noactive);
  transition-property: color, background-color, border-color;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
  cursor: pointer;
}
.tabs__btn:focus {
  border-color: var(--color-active);
  color: var(--color-active);
  background-color: var(--color-black);
}
.tabs__btn:hover {
  border-color: var(--color-white);
  color: var(--color-white);
  background-color: var(--color-black);
}
.tabs__btn:active {
  border-color: var(--color-active);
  color: var(--color-active);
  background-color: var(--color-black);
}
.tabs__btn--active {
  color: var(--color-active);
  background-color: var(--color-white);
}

.categories__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(6, 95px);
  gap: 30px;
}
.categories__item {
  position: relative;
  overflow: hidden;
}
.categories__item--waffen {
  grid-row: 1/span 3;
}
.categories__item--magazine {
  grid-row: 1/span 2;
}
.categories__item--munition {
  grid-row: 3/span 2;
}
.categories__item--waffenteile {
  grid-area: 7/2/5/3;
}
.categories__item--optik {
  grid-row: 4/span 3;
}
.categories__item--zubehor {
  grid-row: 1/span 3;
}
.categories__item--vorankundigung {
  grid-row: 4/span 3;
}
.categories h3 {
  position: absolute;
  left: 50px;
  top: 50px;
  text-transform: uppercase;
  transition: color 0.3s ease-in;
}
.categories a {
  font-weight: 500;
  font-size: 29px;
}
.categories a::before {
  content: "";
  position: absolute;
  border: 1px solid transparent;
  width: 100%;
  height: 100%;
  background-color: var(--color-black);
  opacity: 0.8;
  transition: opacity 0.3s ease-in-out, border-color 0.3s ease-in;
  cursor: pointer;
}
.categories a:focus::before {
  border-color: var(--color-active);
}
.categories a:focus h3 {
  color: var(--color-active);
}
.categories a:hover::before {
  opacity: 0.9;
}

.title {
  margin-bottom: 50px;
  font-weight: 700;
  font-style: normal;
  font-size: 80px;
  line-height: 1.6;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}
.title--main {
  margin-bottom: 10px;
  font-weight: 800;
  font-size: 120px;
}
.title--name {
  font-weight: 500;
  font-size: 29px;
  letter-spacing: 0.05em;
}
.title--card {
  font-weight: 400;
  font-size: 22px;
  letter-spacing: 0.04em;
}

.content {
  font-size: 22px;
  letter-spacing: 0.06em;
}

.btn-reset {
  border-color: transparent;
  background-color: transparent;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--color-active);
  padding: 21px clamp(20px, 2.0625rem, 50px);
  width: fit-content;
  color: var(--color-active);
  background-color: var(--color-white);
}

.search {
  position: relative;
  width: 100%;
  max-width: 300px;
}
.search__btn {
  position: absolute;
  right: 0;
  top: 0;
  width: 20px;
  height: 20px;
  background-color: transparent;
}
.search__input {
  width: 100%;
}
.search__icon {
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
  fill: transparent;
  stroke: var(--color-gray);
}

.nav {
  margin-bottom: 2.1875rem;
}
.nav__list {
  display: flex;
  align-items: center;
}
.nav__item:not(:last-child) {
  margin-right: 3.125rem;
}

.waffen {
  position: relative;
  border: 1px solid transparent;
  padding: clamp(20px, 1.875rem, 30px) clamp(20px, 1.875rem, 30px) clamp(30px, 2.8125rem, 45px);
  font-size: 18px;
  line-height: 1.6;
  letter-spacing: 0.06em;
  color: var(--color-noactive);
  background-color: var(--color-white);
}
.waffen__title {
  color: var(--color-black);
}
.waffen__picture {
  position: relative;
  width: 100%;
}
.waffen__picture span {
  position: absolute;
}
.waffen__picture span:first-of-type {
  right: 0;
  top: 0;
  padding: 5px 10px;
  color: var(--color-white);
  background-color: var(--color-active);
}
.waffen__picture span:last-of-type {
  left: 0;
  bottom: 0;
  border: 1px solid var(--color-active);
  padding: 4px 8px;
  color: var(--color-active);
  background-color: var(--color-white);
}
.waffen__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.waffen__info-code {
  text-transform: uppercase;
}
.waffen__desc {
  display: flex;
  align-items: center;
  margin-bottom: 28px;
  min-height: 60px;
  letter-spacing: 0.055em;
}
.waffen__price {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 30px;
}
.waffen__price-new {
  margin-bottom: 0;
  color: var(--color-active);
}
.waffen__price-old {
  text-decoration: line-through;
}
.waffen__btn {
  position: relative;
  z-index: 1;
  border-color: var(--color-black);
  padding: 10px;
  width: 100%;
  font-size: 22px;
  color: var(--color-black);
  cursor: pointer;
}
.waffen h3 {
  display: flex;
  align-items: center;
  min-height: 70px;
  text-transform: none;
}
.waffen a::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.waffen > *:not(:last-child, .waffen__desc, .waffen__price) {
  margin-bottom: 10px;
}
.waffen:focus {
  border-color: var(--color-active);
}
.waffen:hover {
  border-color: var(--color-active);
}
.waffen:active {
  border-color: var(--color-red);
}

.social {
  display: flex;
}
.social__item:not(:last-child) {
  margin-right: 30px;
}

.contact__list {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 400;
  font-size: 18px;
  letter-spacing: 0.06em;
}
.contact__item {
  position: relative;
  padding-left: 40px;
}
.contact__item::before {
  content: "";
  position: absolute;
  left: 1px;
  top: 0;
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: contain;
  width: 22px;
  height: 20px;
  background-color: var(--color-white);
}
.contact__item a {
  display: block;
}
.contact__item address {
  max-width: 190px;
  line-height: 2.1;
  transform: translateY(-5px);
}
.contact__item > * {
  display: block;
}
.contact__item > *:not(:last-of-type) {
  margin-bottom: 17px;
}
.contact__item--mail::before {
  mask-image: url("../img/sprite.svg#mail");
}
.contact__item--phone::before {
  left: 4px;
  mask-image: url("../img/sprite.svg#phone");
}
.contact__item--pin::before {
  left: 5px;
  top: -2px;
  mask-image: url("../img/sprite.svg#pin");
  width: 26px;
  height: 24px;
}
.contact__item--schedule::before {
  top: -1px;
  mask-image: url("../img/sprite.svg#schedule");
  width: 25px;
  height: 22px;
}

.footer {
  padding: 2.25rem 0;
  background-color: var(--color-black);
}
.footer__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(auto-fit, 1fr);
  row-gap: 3.75rem;
}
.footer__item {
  font-size: 22px;
}
.footer__item--copy {
  width: 100%;
  max-width: 325px;
}
.footer__item--logo {
  align-self: center;
  justify-self: center;
  grid-area: 1/2/3/2;
}
.footer__item--mark {
  display: flex;
}
.footer__item--mark span {
  margin-right: 20px;
}
.footer__item--policy {
  text-align: end;
}
.footer__item--social {
  align-self: center;
  justify-self: end;
}